@import url(../node_modules/normalize.css/normalize.css);

@import url("https://fonts.googleapis.com/css?family=Roboto");

html,
body {
  font-family: "Roboto", sans-serif;
  min-width: 320px;
}

.ms-flex-fix {
  box-flex: 1;
  -ms-flex: 1;
  -moz-box-flex: 1;
  -webkit-box-flex: 1;
}

.header {
  font-size: 24px;
  line-height: 90px;
  height: 90px;
  margin-bottom: 10px;
  text-align: center;
  background-color: #c4c4c4;
}

.flex-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (min-width: 479px) {
  .flex-grid {
    justify-content: space-evenly;
  }
}

.flex-grid--column {
  flex-direction: column;
  margin: 0;
}

@media (min-width: 480px) {
  .flex-grid__item--layout:nth-of-type(1) {
    order: 3;
  }

  .flex-grid__item--layout:nth-of-type(2) {
    order: 4;
  }

  .flex-grid__item--layout:nth-of-type(3) {
    order: 6;
  }

  .flex-grid__item--layout:nth-of-type(4) {
    order: 5;
  }

  .flex-grid__item--layout:nth-of-type(5) {
    order: 7;
  }

  .flex-grid__item--layout:nth-of-type(6) {
    order: 1;
  }

  .flex-grid__item--layout:nth-of-type(7) {
    order: 2;
  }

  .flex-grid__item--layout:nth-of-type(8) {
    order: 8;
  }

  .flex-grid__item--layout:nth-of-type(9) {
    order: 9;
  }

  .flex-grid__item--layout:nth-of-type(10) {
    order: 11;
  }

  .flex-grid__item--layout:nth-of-type(11) {
    order: 13;
  }

  .flex-grid__item--layout:nth-of-type(12) {
    order: 14;
  }

  .flex-grid__item--layout:nth-of-type(13) {
    order: 10;
  }

  .flex-grid__item--layout:nth-of-type(14) {
    order: 12;
  }
}

.tile {
  font-size: 14px;
  position: relative;
  display: flex;
  overflow: hidden;
  box-sizing: border-box;
  width: calc(100% / 2 - 4px);
  min-width: 155px;
  height: 155px;
  min-height: 155px;
  margin-bottom: 10px;
  padding: 15px;
  background-color: #e8ebed;
}

@media (min-width: 480px) {
  .tile {
    width: calc(100vw / 3 - 14px);
    min-width: 150px;
    min-height: 150px;
  }
}

.tile__title {
  z-index: 2;
  transition: 400ms all cubic-bezier(0, 0.3, 0.58, 1);
  align-self: flex-end;
}

.tile__img {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  min-width: 100%;
  height: 100%;
  transition: 400ms all cubic-bezier(0, 0.3, 0.58, 1);
}

.tile__image-link:hover .tile__img {
  filter: blur(10px);
}

.tile__image-link:hover .tile__warning-button {
  opacity: 1;
}

.tile__image-link:hover + .tile__title {
  opacity: 0;
}

.tile__warning-button {
  font-size: 20px;
  position: absolute;
  z-index: 3;
  top: calc(50% - 30px);
  left: calc(50% - 30px);
  display: block;
  width: 60px;
  height: 60px;
  transition: 400ms all cubic-bezier(0, 0.3, 0.58, 1);
  opacity: 0;
  color: #f00;
  border-radius: 50%;
  background-color: #fff;
}

.tile__warning-button:after {
  font-size: 36px;
  font-style: normal;
  line-height: 60px;
  position: absolute;
  width: 100%;
  content: '!';
  text-align: center;
}

.tile--type_wide {
  width: 100%;
}

@media (min-width: 480px) {
  .tile--type_wide {
    width: calc(100vw / 3 * 2 - 14px);
  }
}

.tile--type_square {
  overflow-y: auto;
  width: calc(100% / 3 * 2 - 14px);
  min-width: 200px;
  height: calc(100vw / 3 * 2);
  min-height: 200px;
}

@media (min-width: 480px) {
  .tile--type_square {
    width: calc(100vw / 3 * 2 - 14px);
    height: calc(100vw / 3 * 2 - 14px);
  }
}

.tile--type_small-square {
  width: calc(100vw / 3 - 4px);
  min-width: 100px;
  height: calc(100vw / 3 - 4px);
  min-height: 100px;
}

@media (min-width: 480px) {
  .tile--type_small-square {
    width: calc(100vw / 3 - 14px);
    height: calc(100vw / 3 - 14px);
  }
}

@media (max-width: 479px) {
  .tile--hidden-on-mobile {
    display: none;
  }
}

.tile--text-color_white {
  color: #fff;
}

.tile--color_grey {
  background-color: #c4c4c4;
}

.tile--color_light-grey {
  background-color: #e8ebed;
}

.tile--color_dark-grey {
  background-color: #a2a9b7;
}

.tile--color_red {
  background-color: #f55e62;
}

.tile--color_peach {
  background-color: #ffaa87;
}

.tile--color_orange {
  background-color: #febf26;
}

.tile--color_green {
  background-color: #e3ebd3;
}

.tile--color_blue {
  background-color: #90bce8;
}

.tile--color_violet {
  background-color: #c7abd4;
}

.kinda-table {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.kinda-table__row {
  position: relative;
  margin-bottom: 45px;
  padding: 0;
  border-bottom: 1px dotted #c2c2c2;
}

.kinda-table__column {
  bottom: -5px;
  margin: 0;
  background-color: #e3ebd3;
}

.kinda-table__column--left {
  position: relative;
  margin-right: 7em;
  padding-right: 2px;
}

.kinda-table__column--right {
  position: absolute;
  right: 0;
  padding-left: 2px;
  text-align: right;
}

.chat {
  position: fixed;
  z-index: 9999;
  top: 10%;
  left: 10%;
  visibility: hidden;
  width: 80%;
  height: 80%;
  transition: all 400ms;
  opacity: 0;
}

@media (max-width: 480px) {
  .chat {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.chat:before {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  content: ' ';
  opacity: .75;
  background-color: #000;
}

.chat--opened {
  visibility: visible;
  opacity: 1;
}

.chat .chat__close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.chat .chat__close-button:after {
  content: ' \2573';
  color: #fff;
}

.chat .chat__iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.footer {
  font-size: 24px;
  line-height: 90px;
  height: 90px;
  text-align: center;
  background-color: #c4c4c4;
}




